import React, { Component } from 'react';
import API from "./utils/API";

class News extends Component {
   state = {
    title: "",
    link: ""
  }
   componentDidMount() {
    this.scrapeNews();
  }
  Poop = res => {
        console.log(res.data);
        console.log("poop");
        this.setState({ title: res.data.title, link: res.data.link  })}
    scrapeNews = () => {
    API.scrape()
      .then(res =>{this.Poop(res)})

      .catch(err => console.log(err));
  };

  render() {
    return (
      <div className="App">
      <ul>
      <li>
        {this.state.title}
        {this.state.link}
      </li>
      </ul>

      </div>
    );
  }
}

export default News;



